import React, { Component } from "react";
// import Navbar from "./Navbar";
import Current from "./Current";
import Hourly from "./Hourly";
import Daily from "./Daily";
import Selection from "./Selection";
import axios from "axios";
import dayjs from "dayjs";
import Cookies from "js-cookie";

// helper functions
import { saveHourlyData, saveDailyData, saveCurrentData } from "./helpers.js";

const querystring = require("querystring");
const api = require("../data/api.json");

class Cities extends Component {
  state = {
    timestamp: "",
    cityName: Cookies.getJSON("defaultCity") || "",
    lat: Cookies.getJSON("defaultLat") || "",
    lon: Cookies.getJSON("defaultLon") || "",
    timezone_offset: 0,
    current: {},
    hourly: {},
    daily: {},
    bgStyle: {},
  };

  // fetch from open weather API
  fetchWeatherData = (city, units, lat, lon) => {
    const parameters = {
      appid: api.key,
      units: units,
      lat: lat,
      lon: lon,
      exclude: "minutely",
    };

    const url = `https://api.openweathermap.org/data/2.5/onecall?${querystring.stringify(
      parameters
    )}`;

    return axios.get(url);
  };

  // call fetch data and persist response data
  getData = async (cityName, coordinates) => {
    this.setDefaultCity(cityName, coordinates);
    const lat = coordinates.lat;
    const lon = coordinates.lon;

    const data_metric = await this.fetchWeatherData(
      cityName,
      "metric",
      lat,
      lon
    );
    const data_imperial = await this.fetchWeatherData(
      cityName,
      "imperial",
      lat,
      lon
    );

    if (data_metric && data_imperial) {
      const current = saveCurrentData(data_imperial.data, data_metric.data);
      const hourly = saveHourlyData(
        data_imperial.data.hourly,
        data_metric.data.hourly
      );
      const daily = saveDailyData(
        data_imperial.data.daily,
        data_metric.data.daily
      );

      const style = this.setBgStyle(data_metric.data.current.weather[0].icon);

      const date = dayjs
        .utc(
          (data_imperial.data.current.dt + data_imperial.data.timezone_offset) *
            1000
        )
        .format("MMM DD ddd h:mma");

      let name = "";
      let latitude;
      let longitude;

      if (!Cookies.getJSON("defaultCity")) {
        name = cityName;
        latitude = lat;
        longitude = lon;
      } else {
        name = Cookies.getJSON("defaultCity");
        latitude = Cookies.getJSON("defaultLat");
        longitude = Cookies.getJSON("defaultLon");
      }

      this.setState({
        timestamp: date,
        cityName: name,
        lat: latitude,
        lon: longitude,
        current,
        hourly,
        daily,
        bgStyle: style,
      });
    }
  };

  setDefaultCity = (cityName, coord) => {
    Cookies.set("defaultCity", JSON.stringify(cityName), { expires: 24 });
    Cookies.set("defaultLat", JSON.stringify(coord.lat), { expires: 24 });
    Cookies.set("defaultLon", JSON.stringify(coord.lon), { expires: 24 });
  };

  // set background image based on index from API
  setBgStyle = (code) => {
    const bg_test = require("../data/images/50d.jpg");
    const test = false;

    const bg_default = require("../data/images/01d.jpg");

    let style = {
      backgroundImage: `url(${bg_default})`,
      height: "100%",
      backgroundPosition: "center",
      backgroundRepeat: "no-repeat",
      backgroundSize: "100%",
      animation: "moveBg 30s ease 1s infinite",
    };

    if (test === true) {
      style.backgroundImage = `url(${bg_test})`;
      return style;
    }

    if (code === "01d") {
      const bg_01d = require("../data/images/01d.jpg");
      style.backgroundImage = `url(${bg_01d})`;
    } else if (code === "01n") {
      const bg_01n = require("../data/images/01n.jpg");
      style.backgroundImage = `url(${bg_01n})`;
    } else if (code === "02d") {
      const bg_02d = require("../data/images/02d.jpg");
      style.backgroundImage = `url(${bg_02d})`;
    } else if (code === "02n") {
      const bg_02n = require("../data/images/02n.jpg");
      style.backgroundImage = `url(${bg_02n})`;
    } else if (code === "03d") {
      const bg_03d = require("../data/images/03d.jpg");
      style.backgroundImage = `url(${bg_03d})`;
    } else if (code === "03n") {
      const bg_03n = require("../data/images/03n.jpg");
      style.backgroundImage = `url(${bg_03n})`;
    } else if (code === "04d") {
      const bg_04d = require("../data/images/04d.jpg");
      style.backgroundImage = `url(${bg_04d})`;
    } else if (code === "04n") {
      const bg_04n = require("../data/images/04n.jpg");
      style.backgroundImage = `url(${bg_04n})`;
    } else if (code === "09d") {
      const bg_09d = require("../data/images/09d.jpg");
      style.backgroundImage = `url(${bg_09d})`;
    } else if (code === "09n") {
      const bg_09n = require("../data/images/09n.jpg");
      style.backgroundImage = `url(${bg_09n})`;
    } else if (code === "10d") {
      const bg_10d = require("../data/images/10d.jpg");
      style.backgroundImage = `url(${bg_10d})`;
    } else if (code === "10n") {
      const bg_10n = require("../data/images/10n.jpg");
      style.backgroundImage = `url(${bg_10n})`;
    } else if (code === "11d") {
      const bg_11d = require("../data/images/11d.jpg");
      style.backgroundImage = `url(${bg_11d})`;
    } else if (code === "11n") {
      const bg_11n = require("../data/images/11n.jpg");
      style.backgroundImage = `url(${bg_11n})`;
    } else if (code === "13d") {
      const bg_13d = require("../data/images/13d.jpg");
      style.backgroundImage = `url(${bg_13d})`;
    } else if (code === "13n") {
      const bg_13n = require("../data/images/13n.jpg");
      style.backgroundImage = `url(${bg_13n})`;
    } else if (code === "50d") {
      const bg_50d = require("../data/images/50d.jpg");
      style.backgroundImage = `url(${bg_50d})`;
    } else if (code === "50n") {
      const bg_50n = require("../data/images/50n.jpg");
      style.backgroundImage = `url(${bg_50n})`;
    }

    return style;
  };

  renderCurrent = () => {
    if (this.state.current.IM) {
      return (
        <Current
          cityName={this.state.cityName}
          current={this.state.current}
          timestamp={this.state.timestamp}
        />
      );
    }
  };

  renderHourly = () => {
    if (this.state.hourly.IM) {
      return <Hourly IM={this.state.hourly.IM} ME={this.state.hourly.ME} />;
    }
  };

  renderDaily = () => {
    if (this.state.daily.IM) {
      return <Daily IM={this.state.daily.IM} ME={this.state.daily.ME} />;
    }
  };

  componentDidMount = () => {
    if (this.state.cityName === "") {
      this.getData("New York", { lon: -74.01, lat: 40.71 });
    } else {
      this.getData(this.state.cityName, {
        lon: this.state.lon,
        lat: this.state.lat,
      });
    }
  };

  render() {
    return (
      <React.Fragment>
        <div
          className="d-flex flex-column containter-fluid align-items-center"
          style={this.state.bgStyle}
        >
          {/* <Navbar /> */}
          <Selection getData={this.getData} />
          <div
            className="card container bg-transparent border-0"
            style={({ width: "40rem" }, { margin: "2%" })}
          >
            {this.renderCurrent()}
            {this.renderHourly()}
            {this.renderDaily()}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Cities;
